import React, { Component } from "react";
import { connect } from "react-redux";
import { Col, Card, CardBody, FormGroup } from "reactstrap";
import { Bar, Line } from "react-chartjs-2";
import Loader from "../../components/Loader/Loader";
import * as graphConstant from "../../constants/GraphConstants";
import _ from "lodash";
import * as actions from "../../store/actions";
class Dashboard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      activeUser_Village: {},
      activeUser_Crop: {}
    };
  }
  componentDidMount() {
    //this.props.getCropCultivationHistory();
    let villageArray = [];
    let villageList = [];
    let cropsList = [];
    let compRef = this;
    setTimeout(() => {
      if (
        compRef.props.villages.length > 0 &&
        compRef.props.activeBeneficiaryList.length > 0
      ) {
        compRef.props.villages.forEach(village => {
          villageArray.push({
            Id: village.Id,
            villageName: village.VillageName
          });
          villageList.push(village.VillageName);
        });
        let userCount = [];
        villageArray.forEach(village => {
          let count = 0;
          compRef.props.activeBeneficiaryList.forEach(user => {
            if (user.Village === village.Id) {
              count++;
            }
          });
          userCount.push(count);
        });
        let village = graphConstant.userVillageData;
        village.labels = villageList;
        village.datasets[0].data = userCount;
        compRef.setState({ activeUser_Village: village });
      } else {
        compRef.setState({ activeUser_Village: graphConstant.userVillageData });
      }
      //crop - user data calculations
      setTimeout(() => {
        if (
          compRef.props.cropsList.length > 0 &&
          compRef.props.cropUsersArray.length > 0
        ) {
          compRef.props.cropsList.forEach(crop => {
            cropsList.push(crop.label);
          });
          let newCropUserArray = _.uniqWith(
            compRef.props.cropUsersArray,
            _.isEqual
          );
          let cropUserCount = [];
          cropsList.forEach(crop => {
            let count = 0;
            newCropUserArray.forEach(user => {
              if (user.cropName === crop) {
                count++;
              }
            });
            cropUserCount.push(count);
          });
          let cropsData = graphConstant.userCropsData;
          cropsData.labels = cropsList;
          cropsData.datasets[0].data = cropUserCount;
          compRef.setState({ activeUser_Crop: cropsData });
        } else {
          compRef.setState({ activeUser_Crop: graphConstant.userCropsData });
        }
        compRef.setState({ loading: false });
      }, 3000);
    }, 3000);
  }
  render() {
    return this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <div className="animated fadeIn card-marginTop">
        <FormGroup row>
          <Col xs="12" md="12">
            <Card className="text-white bg-dark">
              <CardBody>
                <div className="text-value">
                  Active beneficiaries by villages
                </div>
              </CardBody>
              <div className="chart-wrapper mx-3 div-padding">
                <Line
                  width={120}
                  height={20}
                  options={graphConstant.userOptions}
                  data={this.state.activeUser_Village}
                />
              </div>
            </Card>
          </Col>
        </FormGroup>
        <FormGroup row style={{ marginTop: -25 }}>
          <Col xs="12" md="12">
            <Card className="text-white bg-danger">
              <CardBody>
                <div className="text-value">Crops</div>
              </CardBody>
              <div className="chart-wrapper mx-6 div-padding">
                <Bar
                  data={this.state.activeUser_Crop}
                  width={120}
                  height={20}
                  options={graphConstant.cropOptions}
                />
              </div>
            </Card>
          </Col>
          {/* <Col xs="12" md="4">
            <Card className="text-white bg-secondary">
              <CardBody>
                <div className="text-value">Small business</div>
              </CardBody>
              <div className="chart-wrapper mx-6 div-padding">
                <Bar
                  data={graphConstant.usersSmallBusinessData}
                  width={100}
                  height={60}
                  options={graphConstant.smallBusinessOptions}
                />
              </div>
            </Card>
          </Col>
          <Col xs="12" md="4">
            <Card className="text-white bg-success">
              <CardBody>
                <div className="text-value">Live stock</div>
              </CardBody>
              <div className="chart-wrapper mx-6 div-padding">
                <Bar
                  data={graphConstant.userLiveStockData}
                  width={100}
                  height={60}
                  options={graphConstant.liveStockOptions}
                />
              </div>
            </Card>
          </Col> */}
        </FormGroup>
      </div>
    );
  }
}
export const mapStateToProps = state => {
  return {
    villages: state.villageReducer.villages,
    activeBeneficiaryList: state.beneficiaryReducer.activeBeneficiaryList,
    cropsList: state.cropsReducer.cropsList,
    cropUsersArray: state.cropsReducer.cropUsersArray
  };
};

export const mapDispatchToProps = dispatch => {
  return {
    getCropCultivationHistory: () =>
      dispatch(actions.getCropCultivationHistory())
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(Dashboard);



